<template>
    <div>
        <adt-search-folder
            :searchRouter="{query: {accountName: search.accountName, organizationId: search.organizationId, phoneNumber: search.phoneNumber}}"
            @on-triggerFolder="searchChange" :isStatus="true">
            <template slot="status">
                <div class="status">
                    <div class="status-flex">
                        <p>状态:</p>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id)" :class="{test: auditStatusNum == id}">
                            {{status.status}}
                        </span>
                    </div>
                </div>
            </template>
            <template slot="always-show">
                <Col span="4" class="search-condition">
                    <span> 场景编码：</span>
                    <Input v-model="search.accountName" clearable></Input>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 场景名称：</span>
                    <Input v-model="search.organizationId" clearable></Input>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 服务类型：</span>
                    <Input v-model="search.phoneNumber" clearable></Input>
                </Col>
                <Col span="4" class="search-condition line-margin">
                    <span> 运输类型：</span>
                    <Input v-model="search.phoneNumber" clearable></Input>
                </Col>
            </template>
        </adt-search-folder>
        <div class="btn-pagina">
            <div>物流全程计划</div>
            <adt-paginator @getPages="getPages" class="pagination" :currentPage="~~page" :total="~~total" path="/authority-manage/user-manage" />
        </div>
        <AdtMyModal title="新增场景" okText="提交审核" cancelText="取消" :showModal="isModalShow" @on-visible-change="addvisibleChange" @on-ok="saveAdd()" @on-cancel="cancelAdd()" width="800">
            <!-- 新增场景 -->
            <div v-if="distinguish == 1">
                <Form class="fromItem" :model="formItem" ref="formItemData" :rules="formItemRule" :label-width="75" label-colon>
                    <FormItem label="场景名称" prop="accountEmail">
                        <Input class="fromItem-content" v-model="formItem.accountEmail"></Input>
                    </FormItem>
                    <FormItem class="fromItem-margin" label="服务类型" prop="organizationId">
                        <Select v-model="formItem.organizationId" class="fromItem-content">
                            <Option v-for="(selectData, id) in formStatus" :key="id" :value="selectData.statusValue">{{selectData.statusValue}}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="fromItem-margin" label="运输类型" prop="accountStatus">
                        <Select v-model="formItem.accountStatus" class="fromItem-content">
                            <Option v-for="(selectData, id) in formStatus" :key="id" :value="selectData.statusKey">{{selectData.statusValue}}</Option>
                        </Select>
                    </FormItem>
                </Form>
            </div>
            <!-- 编辑场景 -->
            <div class="scene-edit" v-if="distinguish == 2">
                <Form class="fromItem" :model="formItem" ref="formItemData" :rules="formItemRule" :label-width="75" label-colon>
                    <FormItem label="场景名称" prop="accountEmail">
                        <Input class="fromItem-content" v-model="formItem.accountEmail"></Input>
                    </FormItem>
                </Form>
                <span>场景编号: {{formItem.num}}</span>
                <span>服务类型: {{formItem.organizationId}}</span>
                <span>运输类型: {{formItem.accountStatus}}</span>
            </div>
            <hr class="line">
            <adtbtnGroup :titleNames="titleNames" :btnIndex="btnIndex" @handleBtn="handleBtn" :width="255">
                <div style="margin-top: 10px;">
                    <!-- 选择基础任务 -->
                    <Table :columns="tableColumnsBasicTask" :data="tableDataBasicTask" v-show="btnIndex == 1" stripe></Table>
                    <!-- 选择附加任务 -->
                    <Table :columns="tableColumnsAdditionalTasks" :data="tableDataAdditionalTasks" v-show="btnIndex == 2" stripe></Table>
                    <!-- 选择单据 -->
                    <Table :columns="tableColumnsDocument" :data="tableDataDocument" v-show="btnIndex == 3" stripe></Table>
                </div>
            </adtbtnGroup>
        </AdtMyModal>
        <Table :columns="tableColumns" :data="tableData" stripe>
            <!-- 审核状态 -->
            <template slot-scope="{ row, index }" slot="auditStatus">
                <span :class="{success: row.statusCode == 'pass', failure: row.statusCode == 'failed'}">{{row.status}}</span>
            </template>
            <!-- 启用状态 -->
            <template slot-scope="{ row, index }" slot="enableStatus">
            </template>
            <!-- 操作 -->
            <template slot-scope="{ row, index }" slot="action">
                <Icon class="icon-action" type="ios-contact-outline" @click="audit()" />
                <Icon class="icon-action" type="md-create" @click="edit(row)" />
                <Icon class="icon-action" type="md-close" @click="remove(row)" />
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template>
        </Table>
        <AdtMyModal title="新增场景审核" okText="同意" cancelText="不同意" :showModal="isAuditShow" @on-visible-change="auditvisibleChange" @on-ok="saveAudit()" @on-cancel="cancelAudit()" width="600">
            <div class="public-detail modal-title">
                <div class="important-info">
                    <Row>
                        <Col span="10">
                            <div class="name">基础任务编号：</div>
                            <div class="content">裂开了裂开</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改人：</div>
                            <div class="content">F545</div>
                        </Col>
                    </Row>
                </div>
                <hr class="line">
                <div class="important-info">
                    <Row>
                        <Col span="10">
                            <div class="name">基础任务编号：</div>
                            <div class="content">裂开了裂开</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改人：</div>
                            <div class="content">F545</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改时间：</div>
                            <div class="content">裂开了了</div>
                        </Col>
                    </Row>
                </div>
                <adtbtnGroup :titleNames="titleNames" :btnIndex="btnIndex" @handleBtn="handleBtn" :width="255">
                    <div style="margin-top: 10px;">
                        <!-- 选择基础任务 -->
                        <Table :columns="tableColumnsBasicTaskAudit" :data="tableDataBasicTaskAudit" v-show="btnIndex == 1" stripe></Table>
                        <!-- 选择附加任务 -->
                        <Table :columns="tableColumnsAdditionalTasksAudit" :data="tableDataAdditionalTasksAudit" v-show="btnIndex == 2" stripe></Table>
                        <!-- 选择单据 -->
                        <Table :columns="tableColumnsDocumentAudit" :data="tableDataDocumentAudit" v-show="btnIndex == 3" stripe></Table>
                    </div>
                </adtbtnGroup>
            </div>
        </AdtMyModal>
        <AdtMyModal title="编辑场景审核" okText="同意" cancelText="不同意" :showModal="isEditAuditShow" @on-visible-change="editAuditvisibleChange" @on-ok="saveEditAudit()" @on-cancel="cancelEditAudit()" width="600">
            <div class="public-detail modal-title">
                <div class="important-info">
                    <Row>
                        <Col span="10">
                            <div class="name">基础任务编号：</div>
                            <div class="content">裂开了裂开</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改人：</div>
                            <div class="content">F545</div>
                        </Col>
                    </Row>
                </div>
                <hr class="line">
                <div class="important-info">
                    <Row>
                        <Col span="10">
                            <div class="name">基础任务编号：</div>
                            <div class="content">裂开了裂开</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改人：</div>
                            <div class="content">F545</div>
                        </Col>
                        <Col span="7">
                            <div class="name">修改时间：</div>
                            <div class="content">裂开了了</div>
                        </Col>
                    </Row>
                </div>
            </div>
            <div class="public-modify-record data-item">
                <div class="collapse">
                    <div slot="content">
                        <div class="content">
                            <p class="list">
                                <span class="content-tit">修改异常名称：</span>
                                <ul>
                                    <li><i class="ico"></i>运输任务</li>
                                </ul>
                                <span><i class="ico ico-blue"></i>物流运输任务</span>
                            </p>
                            <hr class="line">
                            <p class="list del-item">
                                <span class="content-tit">修改异常名称：</span>
                                <ul>
                                    <li>开始时间</li>
                                    <li>结束时间</li>
                                    <li>运输任务</li>
                                </ul>
                            </p>
                            <hr class="line">
                            <p class="list">
                                <span class="content-tit">新增数据项：</span>
                                <ul>
                                    <li>通知企业</li>
                                </ul>
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </AdtMyModal>
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import {tableColumns, tableColumnsBasicTask, tableColumnsAdditionalTasks, tableColumnsDocument, tableColumnsBasicTaskAudit, tableColumnsAdditionalTasksAudit, tableColumnsDocumentAudit} from './data'
import paginator from '@/mixins/paginator'
export default {
    mixins: [paginator],
    data() {
        return {
            isModalShow: false,
            isActionDel: false,
            isAuditShow: false,
            isEditAuditShow: false,
            value: '1',
            // 新增编辑区分  1为新增 2为编辑
            distinguish: 0,
            search: {},
            auditStatusNum: '默认值',
            enableStatusNum: '默认值',
            titleNames: ['选择基础任务', '选择附加任务', '选择单据'],
            btnIndex: 1,
            formItem: {},
            formItemRule: {},
            formStatus: [
                { statusKey: 'enable', statusValue: '可用'},
                { statusKey: 'disable', statusValue: '禁用'}
            ],
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'stock', status: '未货好'},
                {code: 'stock', status: '未货好'},
                {code: 'stock', status: '未货好'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已启用'}
            ],
            tableColumnsBasicTask,
            tableColumnsAdditionalTasks,
            tableColumnsDocument,
            tableColumnsBasicTaskAudit,
            tableColumnsAdditionalTasksAudit,
            tableColumnsDocumentAudit,
            tableDataBasicTaskAudit: [],
            tableDataAdditionalTasksAudit: [],
            tableDataDocumentAudit: [],
            tableDataBasicTask: [],
            tableDataAdditionalTasks: [],
            tableDataDocument: [],
            tableColumns,
            tableData: [
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核通过', statusCode: 'pass'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'failed'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'unreviewed'}
            ]
        }
    },
    methods: {
        getPages (page) {
            this.page = page
        },
        addvisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        auditvisibleChange (changeStatus) {
            this.isAuditShow = changeStatus
        },
        editAuditvisibleChange (changeStatus) {
            this.isEditAuditShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        dataInit () {
            this.getData()
        },
        getData () {},
        add () {
            this.isModalShow = true
            this.distinguish = 1
        },
        searchChange () {},
        // 审核
        audit (row) {
            // this.isAuditShow = true
            this.isEditAuditShow = true
        },
        // 编辑
        edit () {
            this.isModalShow = true
            this.distinguish = 2
        },
        // 删除
        remove () {
            this.isActionDel = true
        },
        allDelClick () {},
        // 新增场景提交审核
        saveAdd () {},
        // 新增场景同意审核
        saveAudit () {},
        // 编辑场景同意审核
        saveEditAudit () {},
        // 新增取消
        cancelAdd () {},
        // 新增审核取消
        cancelAudit () {},
        // 编辑场景审核取消
        cancelEditAudit () {},
        detail (row) {
            this.$router.push({path: '/standard-mapper/scene-manage/detail'})
        },
        auditStatus (index) {
            // 控制状态切换传值
            this.auditStatusNum = index
        },
        enableStatus (index) {
            // 控制状态切换传值
            this.enableStatusNum = index
        },
        // 切换tab
        handleBtn (index) {
            this.btnIndex = index + 1
        }
    },
}
</script>

<style lang="scss" scoped>
.test{
    background-color: $filling-color;
}
span{
    width: 120px;
    line-height: 30px;
}
// p{
//     display: inline-block;
//     font-size: $font-size-large;
//     font-weight: bolder;
//     font-family: $base-font-family;
// }
.fromItem{
    display: flex;
    .fromItem-content{
        width: 170px;
    }
    .fromItem-margin{
        margin-left: 15px;
    }
}
.scene-edit{
    display: flex;
    span{
        margin-left: 15px;
        width: 160px;
    }
}
// 审核状态、启用状态
.status{
    display: flex;
    .status-flex{
        width: 50%;
        p{
            display: inline-block;
            font-size: $font-size-large;
            font-weight: bolder;
            font-family: $base-font-family;
        }
    }
    span{
        padding: 5px 10px;
        margin: 5px 10px;
        cursor: pointer;
        color: $main-font-color;
        &:hover{
            background-color: $filling-color;
        }
        &:active{
            background-color: $filling-color;
        }
    }
}
.line{
    border:0.5px dashed $border-color;
    margin: 0 0 20px 0;
}
.search-condition{
    display: flex;
}
.line-margin {
    margin-left: 30px;
}
.icon-action{
    margin: 0 10px;
}
.btn-pagina{
    display: flex;
    padding: 20px 0;
    justify-content: space-between;
    border-bottom:2px solid #0FB0B8 ;
    .pagination{
        width: 50%;
        text-align: right;
        margin-right: 2%;
    }
}
// 审核的modal的title样式
.modal-title{
    .ivu-row{
        font-size: 14px;
    }
    .line{
        margin: 20px 30px;
    }
}
</style>